:root {
    --background-primary: hsl(37, 33%, 55%);
    --background-primary-darken: hsl(37, 33%, 53%);
    --background-secondary: hsl(36, 16%, 65%);

    --text-primary: hsl(35, 18%, 26%);
    --text-secondary: hsl(349, 75%, 31%);

    --animation-fast: 200ms;
    --border-radius: 2px;
}

* {
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
    font-family: 'Satoshi-Variable';
}

a {
    color: var(--text-primary);
    text-decoration: none;
}

body {
    background-color: var(--background-primary-darken);
    color: var(--text-primary);
    margin: 0 auto;
    overflow-x: hidden;
    width: 100vw;
}

html {
    scroll-behavior: smooth;
}

#PageContent {
    overflow-x: hidden;
    transition: padding var(--animation-fast) ease-in-out;
    width: 100vw;
}

#MainLoader {
    background-color: var(--background-primary-darken);
    height: 100vh;
    left: 0;
    position: absolute;
    top: 0;
    width: 100vw;
    z-index: 1000;

    &.FadeOut {
        animation: FadeoutAnimation 500ms 1 forwards linear;
    }
}

@keyframes FadeoutAnimation {
    to {
        opacity: 0;
    }
}
